* {
  box-sizing: border-box;
}

.App *:hover {
  background-color: rgba(255, 255, 255, 0.1);
  color: white;
}

.App {
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0.8);
  text-align: center;
  height: 100vh;
  padding: 0 5vw;
  color: rgba(255, 255, 255, 0.8);
}


#timeAxis {
  position: absolute;
  top: calc(100vh / 14);
  left: 0;
  /*width: 5vw;*/
  font-size: 2vw;
  z-index: 2;
}

#timeAxis div {
  height: calc(100vh / 14);
}




/*7 days*/
.Day {
  display: inline-grid;
  width: calc(100% / 7);
}

.d_title {
  font-size: 2.3vw;
  height: calc(100vh / 14);
  display: flex;
  align-items: flex-end;
  align-content: space-between;
}




.Hour {
  padding: 0 2px;
  border-top: 1px solid black;
  height: calc(100vh / 14);
}

/*the current day and hour block*/
.active {
  z-index: 15;
  transform: scale(1.2);
  box-shadow: 0 0 10px white;
  background-color: rgba(255, 255, 255, 0.2);
}





.Task {
  border-radius: 50px;
  padding: 4px;
  margin-top: 0.3em;
  position: relative;
  text-shadow: .1vw .1vw  black;
}

.Task:hover .t_detail {
  visibility: visible;
  transition: visibility 0s;
  box-shadow: 2px 2px 4px white;
  z-index: 20;
}
.t_detail {
  font-size: 1.5vw;
  transition: all 0.3s;
  position: absolute;
  top: 0;
  transform: translateX(100%);
  z-index: 1;
  width: 100%;
  padding: 10px 5px;
  background-color: inherit;
  color: inherit;
  border-color: inherit;
  border-radius: 5px;

  border: 1px solid white;
  box-shadow: 1px 1px 4px transparent;

  visibility: collapse;
}
.t_detail:hover {
  background-color: rgba(0, 0, 0, 0.6);
}

.t_detail a {
  display: block;
  color: inherit;
}

.t_title {
  letter-spacing: 1px;
  font-size: 1.5vw;
}

